@mixin list-reset {
  margin: 0;
	padding: 0;
	list-style-type: none;
}

@mixin btn-reset {
	padding: 0;
	margin: 0;
	background: 0;
	border: 0;
	color: inherit;
	text-align: left;
	appearance: none;
}

@mixin link-reset {
	text-decoration: none;
	color: inherit;
	appearance: none;
}

@mixin input-reset {
	padding: 0;
	border: 0;
	appearance: none;
	box-shadow: none;
	border-radius: 0;
}

@mixin full-size {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

@mixin img-fluid {
	max-width: 100%;
	height: auto;
}

@mixin link-arrow {
	a {
		color: inherit;
		text-decoration: none;

		&::after {
			content: url("/img/arrow-link.svg");
			display: inline-block;
			transition: all 0.25s;
			opacity: 0.5;
			margin-left: 2px;
		}

		&:hover::after {
			transform: rotate(45deg);
			opacity: 1;
		}
	}
}

@mixin tablet {
	@media(max-width: 767px) {
		@content;
	}
}

@mixin tabletUpToMobile {
	@media(max-width: 767px) and (min-width: 576px){
		@content;
	}
}

@mixin mobile {
	@media(max-width: 575px) {
		@content;
	}
}
